.listItem{
    object-fit: contain;

    border:solid;
    margin:15px;
    height: 300px;
    width: 300px;

    background-repeat: no-repeat;
    background-position: center; 
    position: relative;
    

}
.listItemContent{
    background-image: linear-gradient(to top,white,rgb(255, 255, 255,0.75));
    width: 100%;
    position: absolute;
    bottom: 0;
}
.listItemButtons{
    padding: 3px;
    width: 100%;
